.forgotPasswordBtn {
  border: none;
  color: #fff;
  background-color: rgb(13, 123, 300);
  font: 300 0.9vmax "Roboto";
  width: 100%;
  padding: 0.8vmax;
  cursor: pointer;
  transition: all 0.5s;
  border-radius: 4px;
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.267);
}

.forgotPasswordBtn:hover {
  background-color: rgb(13, 123, 213);
}

@media screen and (max-width: 600px) {
  .forgotPasswordContainer {
    background-color: #fff;
  }

  .forgotPasswordBox {
    width: 100vw;
    height: 95vh;
  }

  .forgotPasswordForm {
    padding: 5vmax;
  }

  .forgotPasswordForm > div > input {
    padding: 2.5vmax 5vmax;
    font: 300 1.7vmax cursive;
  }

  .forgotPasswordForm > div > svg {
    font-size: 2.8vmax;
  }

  /* 
